import Container from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";
import { Paragraph } from "./styled";

const AuthorPage = () => (
    <Container>
        <Header title={"About the author"} />
        <Section
            title={"Kacper Wojda"}
            body={
                <>
                    <Paragraph>W tym roku skoczyem (<em>wreszcie</em>) technikum i
                        zdecydowaem si na ten kurs, aby poszerzy swoje kompetencje. Profil informatyka oferuje niewiele w
                        ramach webdevu, wic trzeba znale inny sposb na rozwj.</Paragraph>
                    <Paragraph>Pasjonuj si take <b>militariami, grami wideo, TTRPG-ami i muzyk</b>. Ucz si wokalu oraz gry na gitarze i ukulele.</Paragraph>
                </>
            }
        />
    </Container>
);

export default AuthorPage;